import React from "react"

import test_1 from "../assets/test-1.jpg"
import test_2 from "../assets/test-2.jpg"
import test_3 from "../assets/test-3.jpg"

const Carousel = () => {
  return (
    <>
      <h1 className='display-6 text-center my-5'> Testimonials </h1>
      <div
        id='carouselExampleInterval'
        className='container carousel slide bg-white rounded-3'
        data-bs-ride='carousel'>
        <div className='carousel-inner py-4'>
          <div className='carousel-item active' data-bs-interval='4000'>
            <div className='container d-flex flex-column justify-content-center align-items-center'>
              <div className='rounded-circle d-flex flex-column align-items-center'>
                <img
                  src={test_1}
                  className='d-block rounded-circle'
                  style={{ objectFit: "cover", width: "100px", height: "100px" }}
                  alt='...'
                />
                <h5 className='my-2'>Ethan Bond</h5>
                <p>Exceptional service!</p>
                <div>
                  <i className='bi bi-star-fill stars'></i>
                  <i className='bi bi-star-fill stars'></i>
                  <i className='bi bi-star-fill stars'></i>
                  <i className='bi bi-star-fill stars'></i>
                  <i className='bi bi-star-fill stars'></i>
                </div>
              </div>
            </div>
          </div>
          <div className='carousel-item' data-bs-interval='4000'>
            <div className='container d-flex flex-column justify-content-center align-items-center'>
              <div className='rounded-circle'>
                <img
                  src={test_3}
                  className='d-block rounded-circle'
                  style={{ objectFit: "cover", width: "100px", height: "100px" }}
                  alt='...'
                />
              </div>
              <h5 className='my-2'>Marco Pierre</h5>
              <p>I will 100% be coming back!</p>
              <div>
                <i className='bi bi-star-fill stars'></i>
                <i className='bi bi-star-fill stars'></i>
                <i className='bi bi-star-fill stars'></i>
                <i className='bi bi-star-fill stars'></i>
                <i className='bi bi-star-half stars'></i>
              </div>
            </div>
          </div>
          <div className='carousel-item' data-bs-interval='4000'>
            <div className='container d-flex flex-column justify-content-center align-items-center'>
              <div className='rounded-circle'>
                <img
                  src={test_2}
                  className='d-block rounded-circle'
                  style={{ objectFit: "cover", width: "100px", height: "100px" }}
                  alt='...'
                />
              </div>
              <h5 className='my-2'>Ivan Luther </h5>
              <p>Definitely recommend! The service is great!</p>
              <div>
                <i className='bi bi-star-fill stars'></i>
                <i className='bi bi-star-fill stars'></i>
                <i className='bi bi-star-fill stars'></i>
                <i className='bi bi-star-fill stars'></i>
                <i className='bi bi-star-half stars'></i>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default Carousel
